import React, { Component } from 'react';
import Articles from '../Articles/Article.component';
import CTA from '../CTA/CTA.component';
import Pointer from '../Pointer/Pointer.component';
import Questionnaire from '../Questionnaire/Questionnaire.component';
import Title from '../Title/Title.component';
import './Hero.style.scss'

class Hero extends Component {
    constructor(props) {
        super(props);
        this.state = { windowWidth: window.innerWidth };
    }

    handleResize = () => {
        this.setState({ windowWidth: window.innerWidth })
    }

    componentDidMount() {
        window.addEventListener('resize', this.handleResize);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize)
    }

    render() {
        console.log(this.state.windowWidth);
        return (
            <section className='Hero' >
                <section className='Hero__Left-Container' >
                    <Title >Go Green<br/>Save Green</Title>
                    {
                        this.state.windowWidth < 992 ? <Questionnaire style='Mobile' /> : null
                    }
                    <Articles >
                        <Pointer >Fixed Rate pricing.</Pointer>
                        <Pointer >Pay half of what you pay right now.</Pointer>
                    </Articles>
                    <CTA >Schedule Now</CTA>
                </section>
                <section className='Hero__Right-Container' >
                    {
                        this.state.windowWidth >= 992 ? <Questionnaire /> : null
                    }
                </section>
            </section>
        );
    }


};

export default Hero;